import { autorun } from 'mobx'
import { useDisposable } from 'mobx-react-lite'

import { useRoot } from '../../../atoms/RootProvider'
import { useMCreateOrderUpdate } from '../../../graph/generated'
import { useMutationGuard } from '../../../hooks/useMutationGuard'

export function useUpdateOrder() {
  const { createOrder } = useRoot()
  const updateOrder = useMutationGuard(useMCreateOrderUpdate())

  // update order whenever these would change
  useDisposable(() =>
    autorun(() => {
      if (createOrder.orderId) {
        updateOrder({
          ...createOrder.acceptOrderVariables,
          deliveryType: createOrder.deliveryType,
          gps: createOrder.gps,
        })
      }
    }),
  )
}
