import React from 'react'

import { useRoot } from '../../../atoms/RootProvider'
import {
  FOrderMenuRecipeFragment,
  MCreateOrderNewMutation,
  useMCreateOrderAddRecipe,
  useMCreateOrderNew,
  useMCreateOrderRemoveItem,
  useMCreateOrderUpdateItem,
} from '../../../graph/generated'
import { useMutationGuard } from '../../../hooks/useMutationGuard'

export function useOrderCreateControl() {
  const { createOrder } = useRoot()

  const createOrderNew = useMutationGuard(useMCreateOrderNew())
  const addRecipeToOrder = useMutationGuard(useMCreateOrderAddRecipe())
  const updateOrderItem = useMutationGuard(useMCreateOrderUpdateItem())
  const removeOrderItem = useMutationGuard(useMCreateOrderRemoveItem())

  const createOrderNewRef = React.useRef<Promise<MCreateOrderNewMutation | null> | null>(
    null,
  )

  const onCreateOrder = async () => {
    if (createOrder.orderId) {
      return createOrder.orderId
    }
    if (!createOrderNewRef.current) {
      createOrderNewRef.current = createOrderNew(
        createOrder.orderCreateVariables,
      )
    }
    const result = await createOrderNewRef.current
    createOrderNewRef.current = null
    return result ? createOrder.setOrder(result.createOrder.id) : null
  }

  const onRecipeAdd = async (
    recipe: FOrderMenuRecipeFragment,
    withSidedishes: boolean = false,
  ) => {
    const orderId = await onCreateOrder()
    if (!orderId) {
      return
    }
    if (recipe.sidedishCount > 0 && withSidedishes) {
      const {
        sidedishes,
        isConfirmed,
      } = await createOrder.selectSidedishesForRecipe(recipe.id)
      if (isConfirmed) {
        await addRecipeToOrder({ orderId, recipeId: recipe.id, sidedishes })
      }
    } else {
      await addRecipeToOrder({ orderId, recipeId: recipe.id })
    }
  }

  const onItemEdit = async (itemId: ID) => {
    const {
      sidedishes,
      isConfirmed,
    } = await createOrder.selectSidedishesForItem(itemId)
    if (isConfirmed) {
      await updateOrderItem({ itemId, sidedishes })
    }
  }

  const onItemRemove = async (itemId: ID) => {
    await removeOrderItem({ itemId })
  }

  return { onRecipeAdd, onItemEdit, onItemRemove }
}
